* {
  margin: 0;
  padding: 0;
  font-family: 'Poppins',sans-serif;
}

.hero {
  height: 100%;
  width: 100%;
  background-image: linear-gradient(rgba(0,0,0,0.4),rgba(0,0,0,0.4)), url(./assets/bg.jpeg);
  position: absolute;
  background-position: center;
  background-size: cover;
}

.form-box {
  width: 380px;
  height: 480px;
  position: relative;
  margin: 6% auto;
  background: rgb(0, 0, 0);
  padding: 5px;
  opacity: 0.69; /* Changed opacity value */
  border-radius: 30px;

}

.button-box {
  width: 210px;
  margin: 35px auto;
  position: relative;
  box-shadow: 0 0 1px 1px whitesmoke; /* Increased box-shadow */
  border-radius: 30px;
}

.toggle-btn {
  color: aliceblue;
  padding: 10px 30px;
  cursor: pointer;
  background: transparent;
  border: 0;
  outline: none;
  position: relative;
}

#btn {
  top: 0;
  left: 0;
  position: absolute;
  width: 110px;
  height: 100%;
  background: linear-gradient(to right, #ef0303, #00fcdb);
  border-radius: 30px;
  transition: 0.5s;
}

#btn.move {
  left: 100px; /* Adjusted for register form position */
}

.social-icons {
  margin: 30px auto;
  text-align: center;
}

.social-icons img { 
  width: 30px;
  margin: 0 12px;
  box-shadow: 0 0 20px 0 #757575;
  cursor: pointer;
  border-radius: 50%;
}

.input-group {
  top: 180px;
  position: absolute;
  width: 280px;
  transition: 0.5s;
}

.input-field {
  width: 100%;
  padding: 10px 0;
  margin: 5px 0;
  border-left: 0;
  border-top: 0;
  border-right: 0;
  border-bottom: 1px solid #999;
  outline: none;
  background: transparent;
  color: #fff;
}

.input-field:focus {
  border-bottom: 1px solid #fff; 
}

.submit-btn {
  width: 85%;
  padding: 10px 30px;
  cursor: pointer;
  display: block;
  margin: auto;
  background: linear-gradient(to right, #ef0303, #00fcdb); 
  border: 0;
  outline: none;
  border-radius: 30px;
}

.check-box {
  margin: 30px 10px 30px 0;
}

span {
  color: #999;
  font-size: 12px;
  bottom: 68px;
  position: absolute;
}

#login {
  left: 50px;
}

#register {
  left: 50px;
}
